body {
  font-family: Arial;
  padding: 20px;
  background: #f1f1f1;
}

/* Header/Blog Title */
.header {
  padding: 20px;
  font-size: 40px;
  text-align: center;
  background: white;
}

/* Create two unequal columns that floats next to each other */
/* Left column */
.leftcolumn {
  float: left;
  width: 77%;

}

/* Right column */
.rightcolumn {
  float: left;
  width: 20%;
  padding-left: 20px;
}
.card {
  background-color: white;
  padding: 20px;
  margin-top: 20px;
  overflow: auto;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Footer */
.footer {
  padding: 20px;
  text-align: center;
  background: #ddd;
  margin-top: 20px;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.dropdown {
  position: relative;
  display: inline-block;
}



button {
  background-color: blue;
  border: none;
  color: white;
  text-align: center;
  font-size: 16px;
  margin: 4px 0px;
  opacity: 0.6;
  transition: 0.3s;


}
.apparea{
  vertical-align: middle;
  margin: auto;
  align: center;
  display: block;
  clear: both;
  padding-top: 10px;
}

.bar{

  padding: 2px;
  background-color: blue;
  margin-left: 1px;
  margin-right: 1px;
  display: inline-block;

  width:10px;
  float: left;

}
button:hover {opacity: 1}
.dropdown:hover .dropdown-content {display: block;}
.dropdown:hover .dropbtn {opacity: 1}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  align-self: center;
}
.footer{
  clear: both;
}
